<template>
  <div>
    <h2>订单管理</h2>
    <!-- <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
        <a-button type="primary">搜索</a-button>
      </a-col>
    </a-row> -->
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="product" slot-scope="text">
        {{text.product && text.product.name}}
      </template>
      <template slot="address" slot-scope="text">
        {{text.address && text.address.address}}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="toView(text.id)">查看</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="orderVisible"
      title="订单详情"
      width="60%"
      :footer="null"
    >
      <a-spin :spinning="modalLoading">
        <a-form layout="inline">
          <a-form-item label="订单编号">
            {{detail && detail.order_no}}
          </a-form-item>
          <a-form-item label="产品名称">
            {{detail && detail.product && detail.product.name}}
          </a-form-item>
          <a-form-item label="收件人">
            {{detail && detail.address && detail.address.name}}
            {{detail && detail.address && detail.address.phone}}
          </a-form-item>
          <a-form-item label="地址">
            {{detail && detail.address && detail.address.address}}
          </a-form-item>
          <a-form-item label="数量">
            {{detail && detail.nums}}
          </a-form-item>
          <a-form-item label="支付金额">
            {{detail && detail.amount}}
          </a-form-item>
          <a-form-item label="订单状态">
            {{detail && detail.status}}
          </a-form-item>
        </a-form>
      </a-spin>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '订单标号',
        dataIndex: 'order_no'
      },
      {
        title: '产品名称',
        scopedSlots: { customRender: "product"},
      },
      {
        title: '地址',
        scopedSlots: { customRender: "address"},
      },
      {
        title: '数量',
        dataIndex: 'nums'
      },
      {
        title: '下单时间',
        dataIndex: 'create_time'
      },
      {
        title: '状态',
        dataIndex: 'status'
      },
      {
        title: '备注',
        dataIndex: 'remark'
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      orderVisible: false,
      modalLoading: false,
      loadData: paramters => {
        return this.getOrderList(
          Object.assign({}, paramters, {shop_id: sessionStorage.getItem('shop_id') ? Number(sessionStorage.getItem('shop_id')): undefined})
        )
      }
    }
  },
  computed: {
    ...mapState('order', ['detail'])
  },
  methods: {
    ...mapActions('order', {
      getOrderList: 'getList',
      getOrderDetail: 'getDetail'
    }),
    toView(id) {
      this.$store.commit('order/SET_DETAIL', {})
      this.modalLoading = true
      this.orderVisible = true
      this.getOrderDetail(id).then(res => {
        this.modalLoading = false
      })
    },
  }
}
</script>
<style>
.ant-form-item {
  width: 100%;
}
</style>